	module.exports = {"data":{"site":{"siteMetadata":{"title":"rafael.do","author":"Rafael E. Belliard"}},"markdownRemark":{"id":"/Users/rafael/play/rebelliard.github.io/src/pages/blog/2016-07-16-response-to-react-design-principles-interpretation-by-react-members/index.md absPath of file >>> MarkdownRemark","html":"<script async src=\"//platform.twitter.com/widgets.js\" charset=\"utf-8\"></script>\n<p>Yesterday, I shared <a href=\"react-driven-by-facebook-principle-concerns\">how concerned</a> I was with React‚Äôs\n<a href=\"https://facebook.github.io/react/contributing/design-principles.html\">Design Principles</a>.\nToday, I‚Äôd like to share the response I received from <a href=\"https://twitter.com/dan_abramov\">@dan_abramov</a>\n‚Äîa member of the React team‚Äî regarding my interpretation of these principles.</p>\n<p>I hope this helps anyone who felt as conflicted as I did.</p>\n<h2>Response</h2>\n<blockquote class=\"twitter-tweet\" data-lang=\"en\"><p lang=\"en\" dir=\"ltr\"><a href=\"https://twitter.com/rebelliard\">@rebelliard</a> That Facebook heavily uses React in production (unlike Parse) is exactly why I think community can bet on it.</p>&mdash; Dan Abramov (@dan_abramov) <a href=\"https://twitter.com/dan_abramov/status/754278530817617921\">July 16, 2016</a></blockquote>\n<blockquote class=\"twitter-tweet\" data-conversation=\"none\" data-lang=\"en\"><p lang=\"en\" dir=\"ltr\"><a href=\"https://twitter.com/rebelliard\">@rebelliard</a> Community is obviously important but ultimately projects with business value are those that companies keep developing.</p>&mdash; Dan Abramov (@dan_abramov) <a href=\"https://twitter.com/dan_abramov/status/754278857331662848\">July 16, 2016</a></blockquote>\n<blockquote class=\"twitter-tweet\" data-conversation=\"none\" data-lang=\"en\"><p lang=\"en\" dir=\"ltr\"><a href=\"https://twitter.com/rebelliard\">@rebelliard</a> So on the contrary, I think you should have been concerned if Facebook didn&#39;t use React and maintained it for image purposes üòâ</p>&mdash; Dan Abramov (@dan_abramov) <a href=\"https://twitter.com/dan_abramov/status/754279261805109248\">July 16, 2016</a></blockquote>\n<blockquote class=\"twitter-tweet\" data-conversation=\"none\" data-lang=\"en\"><p lang=\"en\" dir=\"ltr\"><a href=\"https://twitter.com/rebelliard\">@rebelliard</a> There is also some sort of misunderstanding here. Post says ‚Äúboth externally and internally‚Äù. <a href=\"https://t.co/ku7DG0j5S0\">pic.twitter.com/ku7DG0j5S0</a></p>&mdash; Dan Abramov (@dan_abramov) <a href=\"https://twitter.com/dan_abramov/status/754280725076119552\">July 16, 2016</a></blockquote>\n<blockquote class=\"twitter-tweet\" data-conversation=\"none\" data-lang=\"en\"><p lang=\"en\" dir=\"ltr\"><a href=\"https://twitter.com/rebelliard\">@rebelliard</a> ‚ÄúExternally‚Äù *is* the community. Sorry if this wasn‚Äôt clear. I can change the wording to be more direct.</p>&mdash; Dan Abramov (@dan_abramov) <a href=\"https://twitter.com/dan_abramov/status/754281037891592192\">July 16, 2016</a></blockquote>\n<blockquote class=\"twitter-tweet\" data-lang=\"en\"><p lang=\"en\" dir=\"ltr\"><a href=\"https://twitter.com/rebelliard\">@rebelliard</a> I wrote a comment</p>&mdash; Dan Abramov (@dan_abramov) <a href=\"https://twitter.com/dan_abramov/status/754285961845022720\">July 16, 2016</a></blockquote>\n<ul>\n<li>The comment can be read <a href=\"react-driven-by-facebook-principle-concerns#comment-2786435370\">here</a>.\nIt‚Äôs good stuff.</li>\n</ul>\n<h2>About Parse</h2>\n<blockquote class=\"twitter-tweet\" data-lang=\"en\"><p lang=\"en\" dir=\"ltr\"><a href=\"https://twitter.com/rebelliard\">@rebelliard</a> <a href=\"https://twitter.com/dan_abramov\">@dan_abramov</a> Parse servers will be decommissioned in a year and the entire stack was made open source so you can run your own</p>&mdash; Vjeux (@Vjeux) <a href=\"https://twitter.com/Vjeux/status/754317380386304004\">July 16, 2016</a></blockquote>\n<blockquote class=\"twitter-tweet\" data-conversation=\"none\" data-lang=\"en\"><p lang=\"en\" dir=\"ltr\"><a href=\"https://twitter.com/rebelliard\">@rebelliard</a> <a href=\"https://twitter.com/dan_abramov\">@dan_abramov</a> I&#39;ve rarely seen such a graceful shut down of a service before</p>&mdash; Vjeux (@Vjeux) <a href=\"https://twitter.com/Vjeux/status/754317595143049222\">July 16, 2016</a></blockquote>\n<h2>Closing thoughts</h2>\n<p>\n  <img src=\"/public/images/blog/2016-07-16/not-even-mad.jpg\" height=\"185\" alt=\"That's amazing.\">\n</p>\n<p>I thank <a href=\"https://twitter.com/dan_abramov\">@dan_abramov</a> for taking the time to clarify what they meant.\nIt certainly didn‚Äôt occur to me that they kept an ear to the ground and I could <em>just ask</em>,\nwhich gives me a sense of relief and quite frankly, makes me <strong>even more</strong> interested\nin the library than I previously was.</p>\n<ul>\n<li><a href=\"https://twitter.com/dan_abramov/status/754278530817617921\">Response</a> on Twitter.</li>\n<li><a href=\"react-driven-by-facebook-principle-concerns#comment-2786435370\">Response</a> to my original post.</li>\n<li><a href=\"https://github.com/facebook/react/pull/7292/files\">Changeset</a> <em>further</em> clarifying\nthe original article.</li>\n</ul>","frontmatter":{"title":"Response to my interpretation of React's ‚ÄúDesign Principles‚Äù","date":"July 16, 2016","permalink":"/blog/response-to-react-design-principles-interpretation-by-react-members/"}}},"pathContext":{"slug":"/blog/response-to-react-design-principles-interpretation-by-react-members/","previous":{"fields":{"slug":"/blog/react-driven-by-facebook-principle-concerns/"},"frontmatter":{"title":"React's ‚ÄúDriven by Facebook‚Äù principle concerns me","teaser":"React's ‚ÄúDesign Principles‚Äù article gave me a rollercoaster of emotions that reminded me of Facebook's ill-fated Parse service.","description":"React's ‚ÄúDesign Principles‚Äù article gave me a rollercoaster of emotions that reminded me of Facebook's ill-fated Parse service.","tags":["react"],"redirectFrom":null}},"next":{"fields":{"slug":"/blog/8-tips-to-become-an-effective-remote-developer/"},"frontmatter":{"title":"8 tips to become an effective remote developer","teaser":"For you to evolve as a developer, you need to be a self-starter and make it a point to keep yourself active and in constant state of learning.","description":"For you to evolve as a developer, you need to be a self-starter and make it a point to keep yourself active and in constant state of learning.","tags":["growth"],"redirectFrom":null}}}}

